import styled from '@emotion/styled';
import { useModal } from '../Hooks/useModal'
import { ModalTypes } from '../Types';
import { Div } from 'Elements';

export const Button: ModalTypes['Parts']['Button'] = (props) => {
  const { type, afterClick, children = "Button" } = props

  const { closeModal } = useModal()

  const handleClick = async () => {
    if (type === 'Close') {
      closeModal();
    }
    if (afterClick) {
      const fn = typeof afterClick === 'function' ? afterClick : afterClick[type];
      if (fn) {
        const result = fn();
        if (result instanceof Promise) {
          await result;  // 비동기 함수일 경우
        }
        // else {} 동기 함수일 경우 이미 위에서 실행됨
      }
    }
  };
  
  // handleClick 함수 분석
  // Close 타입 체크: if (type === 'Close') { closeModal(); }

  // 이 부분은 버튼의 type이 'Close'인 경우 모달을 닫도록 하는 로직입니다. closeModal은 모달을 닫는 함수입니다.
  // afterClick 체크: if (afterClick) { ... }

  // 이 부분은 afterClick이 정의되어 있다면 실행되는 로직입니다.
  // 함수 타입 체크: const fn = typeof afterClick === 'function' ? afterClick : afterClick[type];

  // afterClick이 함수인지 아닌지 체크합니다.
  // 만약 함수라면, 그대로 fn에 할당합니다.
  // 함수가 아니라면, afterClick[type]을 fn에 할당합니다. 이 경우 afterClick는 Record<ModalButton_Type, ModalButton_AfterClick> 타입이라고 가정합니다.
  // fn 실행:

  // 만약 fn이 존재한다면 실행합니다.
  // const result = fn();
  // 함수를 실행하고 그 결과를 result에 저장합니다.
  // Promise 체크: if (result instanceof Promise) { await result; }

  // result가 Promise인지 체크합니다.
  // 만약 Promise라면, await를 사용하여 비동기 작업이 완료될 때까지 기다립니다.
  // 이렇게 하면 afterClick에 동기 함수든 비동기 함수든 넣을 수 있고, type에 따라 다른 함수를 실행할 수도 있습니다.


  return (
    <ButtonWrapper onClick={handleClick}>
      {children}
    </ButtonWrapper>
  )
}

const ButtonWrapper = styled(Div)<ModalTypes['Props']['Button']>`
  cursor: pointer;

  &:hover {
    color: #007BFF;
  }
`